.container {
  margin: 40px;
  margin-top: 0;
  padding: 20px;
}
.container2 {
  width: 750px;
  position: relative;
  margin: auto;
  height: 300px;
}
.container2 > img {
  width: 100%;
  height: 100%;
}
.face {
  z-index: 1150;
  /* top: 0;
    left: 0; */
  border: 2px solid white;
  border-radius: 100%;
  cursor: pointer;
  width: 45px !important;
  height: 45px !important;
}
.boxx {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.box2 {
  position: absolute;
  /* right: 0; */
}
.box2:nth-child(1) {
  left: 10%;
  top: 10%;
}
.box2:nth-child(2) {
  left: 60%;
  top: 10%;
}
.box2:nth-child(3) {
  left: 15%;
  top: 55%;
}
.box2:nth-child(4) {
  left: 42%;
  top: 50%;
}
.box2:nth-child(5) {
  right: 10%;
  top: 45%;
}
.title {
  margin-top: 50px;
  margin-bottom: 20px;
  text-align: center;
}
.title2 {
  margin-bottom: 40px;
  padding: 0 10px;
  color: gray;
}
.title > span {
  padding-bottom: 5px;
  border-bottom: 2px solid aqua;
}
.info::before {
  content: "";

  display: block;
  width: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 15px solid white;
  /* margin: auto; */
  position: absolute;
  top: -10px;
  right: 45%;
}
.info {
  position: relative;
  border: 1px solid #eee;
  box-shadow: 1px 2px 2px gray;
  /* z-index: 11; */
  /* padding: 5px; */
  margin-top: 5px;
  background-color: white;
  /* width: 150px;
  */
  /* width: 1px; */
  border-radius: 10px;
  padding: 5px 10px;
  width: 150px;
  transition: opacity 0.2s ease-in;
  font-size: 12px;
}
.info > h3 {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 0;
  /* color: red; */
}
.info > h5 {
  text-align: left;
  line-height: 15px;
}
@media screen and (max-width: 700px) {
  .container {
    margin: 0px;
    padding: 0;
  }
  .container2 {
    /* display: none; */
    width: 90%;
  }
}
@media screen and (max-width: 500px) {
  .box2:nth-child(1) {
    left: 0%;
    top: 10%;
  }
  .box2:nth-child(2) {
    left: 50%;
    top: 15%;
  }
  .box2:nth-child(3) {
    left: 0%;
    top: 55%;
  }
  .box2:nth-child(4) {
    left: 25%;
    top: 50%;
  }
  .box2:nth-child(5) {
    right: -5%;
    top: 45%;
  }
}
