.button {
  background-color: #23d4ce;
  border-radius: 3px;
  padding: 10px 15px;
  font-size: 12px;
  cursor: pointer;
}
.button:nth-child(1) {
  color: #23d4ce;
  margin-right: 5px;
  background-color: white;
}
.authContainer {
  display: flex;
  flex-wrap: nowrap;
  flex-shrink: 0;
  margin-left: auto;
}
.world > img {
  margin-right: 2px;
  vertical-align: bottom;
}
.world {
  cursor: pointer;
  font-size: 14px;
  margin-right: 15px;
  color: white;
}
.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  border-bottom: 1px solid white;
}
.container2 {
  align-items: center;
  margin: auto;
  height: 60px;
  width: 85%;
  display: flex;
  align-items: center;

  justify-content: space-between;
}
.brand {
  display: flex;
  justify-content: center;
  align-items: center;
}
.list {
  margin: 0;
  margin-left: auto;
  list-style: none;
  justify-content: space-between;
  display: flex;
  padding: 0;
}
.list > li > a {
  color: white;
}
.list li {
  font-weight: bold;
  font-size: 12px;
  padding: 0 15px;
  transition: font-size 0.3s ease-in;
}
.list li a:hover {
  color: #23d4ce;
}

.rlist {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}
.active {
  font-size: 20px !important;
  background-image: linear-gradient(
    to right,
    #6372ff 0%,
    #5ca9fb 100%
  ) !important;
  background-clip: text;
  color: transparent;
}
.close {
  width: 100%;
  padding: 10px;
  font-size: 25px;
  display: none;
  text-align: right;
}
.toggle {
  display: none;
}
@media screen and (max-width: 900px) {
  .close {
    display: block;
  }
  .container2 {
    width: 100%;
  }
  .brand {
    padding-left: 10px;
  }
}
@media screen and (max-width: 900px) {
  .rlist {
  }
  .authContainer {
    padding: 20px;
    width: 100%;
    border: 1px solid black;
    justify-content: space-between;
  }
  .rlist {
    position: fixed;
    top: 0px;
    background: linear-gradient(to right, #233155 0%, #39224d 100%) !important;

    right: -1000px;
    flex-direction: column-reverse;
    width: 100%;
    height: 100%;

    overflow: hidden;
    justify-content: flex-end;
    transition: all 0.4s ease-in;
  }
  .list {
    width: 100%;
    flex-direction: column;
  }
  /* .list > li:nth-child(1) {
    border-top: 1px solid black;
  } */
  .list li {
    user-select: none;
    padding: 20px;
    /* border-bottom: 1px solid black; */
  }
  .list li:hover {
    color: black;
  }
  .list li > a {
    color: black;
    font-size: 20px;
    /* color: #23d4ce; */
  }
  .toggle {
    display: flex;
    font-size: 35px;
    padding: 10px;
  }
  .active {
    color: black;
    font-size: inherit !important;
    background-color: red !important;
  }
  .active::after {
    height: 0 !important;
  }
  .list li::after {
    display: none !important;
  }
}
