}
.box3 {
  height: 85px;
  padding-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.box {
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
}
.title {
  margin-bottom: 50px;
  text-align: center;
}
.title > span {
  padding-bottom: 5px;
  border-bottom: 2px solid aqua;
}
.container2 {
  border: 1px solid #eee;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 20%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container2 > p {
  text-align: center;
  line-height: 22px;
}
.container2 > h3 {
  margin-bottom: 20px;
}
@media screen and (max-width: 900px) {
  .container {
    margin: 10px;
    /* padding: 0; */
  }
  .box {
    /* display: none; */
  }
  .container2 {
    width: 80%;
    margin-top: 20px;
  }
}
