.container {
  /* border: 1px solid black; */
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
  padding: 0 20px;
  justify-content: space-around;
}
.imageContainer {
  width: 120px;
  /* height: 80px; */
  /* padding: 20px; */
  margin-bottom: 40px;
}
.imageContainer > img {
  width: 100%;
  /* height: 100%; */
}
@media screen and (max-width: 1000px) {
  .imageContainer {
    width: 40%;

    /* border: 1px solid red; */
    /* padding: 30px;*/
  }
  .imageContainer > img {
    width: 120px;
  }
}
