.container {
  height: 550px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;

  display: flex;
  align-items: center;
}
.xx {
  top: 0;
  left: 0;
  z-index: -11;
  width: 100%;
  height: 100%;

  position: absolute;
}
.phoneContainer {
  align-items: center;
  justify-content: right;
}
.box2 {
  width: 100px;
}
.infoContainer {
  width: 40%;
  color: white;
  margin: 0;
  margin-left: 60px;
}
.infoContainer > div {
  /* width: 70%; */
  white-space: pre-wrap;
  text-align: left;
}
.infoContainer > div > h1 {
  margin: 0;
  font-size: 28px;
  line-height: 40px;
}
.infoContainer > div > h3 {
  line-height: 30px;
  margin: 10px 0;
  font-size: 16px;
  margin-bottom: 25px;
}
.button {
  margin-top: 55px;
  background-color: #23d4ce;
  border-radius: 3px;
  padding: 10px;
  font-size: 12px;
}
@media screen and (max-width: 850px) {
  .container {
    height: 450px;
  }
  .infoContainer {
    margin-left: 40px;
    width: 50%;
  }

  .infoContainer > div > h1 {
    font-size: 1.3em;
    line-height: 25px;
  }
  .infoContainer > div > h3 {
    font-size: 0.8em;
    line-height: 20px;
  }
}
@media screen and (max-width: 550px) {
  .container {
    height: 300px;
  }

  .infoContainer > div > h1 {
    font-size: 0.8em;
    line-height: 20px;
  }
  .infoContainer > div > h3 {
    font-size: 0.5em;
    line-height: 15px;
    margin: 10px 0;
  }

  .infoContainer {
    margin: 0;
    margin-left: 30px;
  }
  .button {
    margin-top: 0;
    padding: 5px 10px;
    font-size: 10px;
  }
}
@media screen and (max-width: 250px) {
  .infoContainer {
    display: none;
  }
}
